@import "./reset.scss";

@keyframes stretch {
    0% {
      transform: scale(0.8);
    }
    50% {
        transform: scale(1.2);
    }
    100% {
      transform: scale(0.8);
    }
  }
.ecomMB {
    position: relative;
    left: 50%;
    transform: translate(-50%);
    width: 320px;
    height: 500px;
    background: #ffffff;
    border: 1px solid #e7e7e7;
    .adx-logo {
        position: absolute;
        top: 0;
        right: 0;
        cursor: pointer;
    }
    .ecomMB-bg {
        width: 320px;
        height: 180px;
        img {
            width: 320px;
            height: 180px;
        }
    }
}
.slider-down {
    overflow: hidden;
    overflow-y: auto;
    scroll-snap-type: y mandatory;
    &::-webkit-scrollbar {
        display: none;
    }
}
.ecomMB-product {
    margin-top: 5px;
    height: 290px;
    transition: all 1s;
    .product-item {
        margin: 7px 10px 0px 12px;
        display: flex;
    }
    .item-left {
        .content-img {
            position: relative;
            height: 90px;
            width: 90px;
            img {
                width: 100%;
                height: 100%;
            }
            .content-svg {
                position: absolute;
                bottom: 68px;
                right: 0;
            }
            .content-svg-text {
                font-weight: bold;
                font-size: 13px;
                line-height: 18px;
                color: #ffffff;
                position: absolute;
                top: 0;
                right: 2px;
            }
        }
    }
    .item-right {
        margin-top: 4px;
        margin-left: 12px;
        .item-detail {
            h5 {
                font-size: 14px;
                line-height: 17px;
                display: flex;
                align-items: center;
                color: #373737;
                font-weight: 400;
            }
        }
        .item-price {
            margin-top: 6px;
            display: flex;
            h5 {
                margin-right: 2px;
                font-size: 14px;
                font-weight: 300;
                line-height: 18px;
                text-align: center;
                color: #777777;
            }
            h4 {
                font-size: 16px;
                font-weight: 500;
                line-height: 18px;
                text-align: center;
                color: #e00000;
            }
        }
    }
}
.content-brand {
    display: flex;
    margin: 2px 12px;
    position: relative;
    .brand-left {
        position: absolute;
        left: 0;
    }
    .brand-center {
        position: absolute;
        left: 50%;
        cursor: pointer;
        animation-name: stretch;
        animation-duration: 0.5s; 
        animation-timing-function: ease-out; 
        animation-delay: 0;
        animation-direction: alternate;
        animation-iteration-count: infinite;
        animation-fill-mode: none;
        animation-play-state: running;
    }
    .brand-right {
        position: absolute;
        right: 0;
    }
}
